const galleryContainer = document.querySelector('.gallery');

function createGallery(imagesArray) {
  const galleryItems = imagesArray.map(image => {
    const li = document.createElement('li');
    li.classList.add('gallery-item');

    const a = document.createElement('a');
    a.classList.add('gallery-link');
    a.href = image.original;

    const img = document.createElement('img');
    img.classList.add('gallery-image');
    img.src = image.preview;
    img.alt = image.description;
    img.dataset.source = image.original;

    a.appendChild(img);
    li.appendChild(a);
    return li;
  });

  const galleryFragment = document.createDocumentFragment();
  galleryItems.forEach(item => {
    galleryFragment.appendChild(item);
  });

  galleryContainer.appendChild(galleryFragment);
}

createGallery(images);
galleryContainer.addEventListener('click', onGalleryItemClick);

function onGalleryItemClick(event) {
  event.preventDefault();

  if (event.target.nodeName !== 'IMG') {
    return;
  }

  const largeImageURL = event.target.dataset.source;
  const largeAlt = event.target.alt;

  const instance = basicLightbox.create(
    `<img src="${largeImageURL}" class="large-image" alt="${largeAlt}">`,
    {
      onShow: instance => {
        const onKeyUp = event => {
          if (event.code === 'Escape') {
            instance.close();
          }
        };

        document.addEventListener('keyup', onKeyUp);
        instance.onClose = () => {
          document.removeEventListener('keyup', onKeyUp);
        };
      },
    }
  );

  instance.show();
}
